
* {
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
* {
  -webkit-tap-highlight-color: transparent;
}
[v-cloak] {
  display: none!important;
}
body,
dl,
dd,
ul,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
form,
p {
  margin: 0;
}
html {
  width: 100%;
  height: 100%;
}
body {
  width: 100%;
  line-height: 1;
  color: #000000;
  height: 100%;
  font-size: 12px;
  font-family: "Microsoft YaHei", PingFangSC, Arial, "宋体", Helvetica, sans-serif;
  text-align: center;
  background: #13142f;
}
ol,
ul {
  padding: 0;
}
h1,
h2,
h3,
h4,
h5 {
  font-size: inherit;
  font-weight: normal;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
ul,
ol,
li {
  list-style: none;
}
img {
  border: 0;
  -ms-interpolation-mode: bicubic;
  vertical-align: top;
  /*禁止保存*/
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
/* 默认链接 */
a {
  display: block;
  text-decoration: none;
  outline: none;
  /* FF Opera */
  position: relative;
  -webkit-tap-highlight-color: transparent;
  /* 给移动端页面加上:active伪类,js中给body绑定touchstart事件，处理函数为空即可 */
}
a:hover {
  text-decoration: none;
}
#app {
  width: 100%;
  position: relative;
}
#app .bgImg {
  width: 100%;
  display: block;
}
#app .box {
  position: relative;
  width: 100%;
  background: #fff;
  border: none;
}
#app .box .download-content {
  position: absolute;
  top: 65%;
  left: 50%;
  -webkit-transform: translateX(-50%);
     -moz-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
       -o-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 100%;
}
#app .box a {
  display: block;
  width: 65%;
  margin: 0 auto;
}
#app .box a img {
  width: 100%;
}
.down {
  -webkit-animation: down 1s 1 linear forwards;
     -moz-animation: down 1s 1 linear forwards;
       -o-animation: down 1s 1 linear forwards;
          animation: down 1s 1 linear forwards;
}
@-webkit-keyframes down {
0% {
    top: 0;
}
100% {
    top: 110px;
}
}
@-moz-keyframes down {
0% {
    top: 0;
}
100% {
    top: 110px;
}
}
@-o-keyframes down {
0% {
    top: 0;
}
100% {
    top: 110px;
}
}
@keyframes down {
0% {
    top: 0;
}
100% {
    top: 110px;
}
}
.androidBg img {
  width: 100%;
}
.iosBg img {
  width: 100%;
}
.fade-enter-active,
.fade-leave-active {
  -o-transition: opacity .5s;
  -moz-transition: opacity .5s;
  transition: opacity .5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
#app {
  width: 100%;
  height: 100%;
  background: url('image/wxbg.png') no-repeat top left;
  -moz-background-size: 100% auto;
    -o-background-size: 100% auto;
       background-size: 100% auto;
  background-color: #fff;
}
img {
  border: none;
}



